import React, { useState, useRef } from "react";
import { LuckyWheel } from "@lucky-canvas/react";
import { wheelItems } from "../data/memos";

export default function LuckyWheelComponent() {
  const [blocks] = useState([{ padding: "10px", background: "#869cfa" }]);
  const [prizes] = useState([
    {
      background: "#e9e8fe",
      fonts: [{ text: "小烧烤", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 20,
    },
    {
      background: "#b8c5f2",
      fonts: [{ text: "小米粉", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 20,
    },
    {
      background: "#e9e8fe",
      fonts: [{ text: "娜娜指定节目", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 10,
    },
    {
      background: "#b8c5f2",
      fonts: [{ text: "小奶茶", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 10,
    },
    {
      background: "#e9e8fe",
      fonts: [{ text: "再转一次", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 10,
    },
    {
      background: "#b8c5f2",
      fonts: [{ text: "娜娜自选", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 20,
    },
    {
      background: "#F1F3F4",
      fonts: [{ text: "下次再来", fontStyle: "Ma Shan Zheng", top: 10 }],
      range: 10,
    },
  ]);
  const [buttons] = useState([
    { radius: "40%", background: "#617df2" },
    { radius: "35%", background: "#afc8ff" },
    {
      radius: "30%",
      background: "#869cfa",
      pointer: true,
      fonts: [{ text: "开始", top: "-10px", fontStyle: "Ma Shan Zheng" }],
    },
  ]);
  const myLucky = useRef();
  return (
    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
      <LuckyWheel
        ref={myLucky}
        width="300px"
        height="300px"
        blocks={blocks}
        prizes={prizes}
        buttons={buttons}
        onStart={() => {
          // 点击抽奖按钮会触发star回调
          myLucky.current.play();
          setTimeout(() => {
            const index = (Math.random() * 6) >> 0;
            myLucky.current.stop(index);
          }, 2500);
        }}
        onEnd={(prize) => {
          // 抽奖结束会触发end回调
          if (prize.fonts[0].text === "再转一次") {
            alert("再转一次！");
          }

          if (prize.fonts[0].text === "下次再来") {
            alert("运气有点不好哦，给yy撒个娇可以再获得一次抽奖的机会哦~！");
          }
          alert("恭喜你抽到 " + prize.fonts[0].text + " 奖品！请找yy兑换！如果不满意奖品的话，可以撒个娇再获得一次抽奖的机会~");
        }}
      />
    </div>
  );
}
